import * as React from "react";
import Header from "./Header";
import theme from "./styles/theme";
import { ThemeProvider } from "@mui/material/styles";
import Hero from "./Hero";
import { Typography, Grid, Button } from "@mui/material";
import galaxy from "./img/galaxyBG.png";
import TwitterIcon from "@mui/icons-material/Twitter";
import { ReactComponent as Discord } from "./img/discord.svg";
import PrivacyPolicy from "./PrivacyPolicy";

const App = () => {
  const [showPolicy, setShowPolicy] = React.useState(false);
  React.useEffect(() => {
    document.title = "Lypher App";
  }, []);
  React.useEffect(() => {
    if (window.location.href.indexOf("#privacy-policy") !== -1) {
      setShowPolicy(true);
    } else {
      setShowPolicy(false);
    }
  }, []);
  const handlePolicy = () => {
    if (window.location.href.indexOf("#privacy-policy") === -1) {
      window.location.hash = "#privacy-policy";
      return setShowPolicy(true);
    }
    if (window.location.href.indexOf("#privacy-policy") !== -1) {
      window.location.hash = "";
      return setShowPolicy(false);
    }
  };
  return (
    <ThemeProvider theme={theme}>
      <Grid
        spacing={2}
        sx={{
          background: "green",
          height: window.innerWidth > "900px" ? "100vh" : undefined,
          backgroundImage: `url(${galaxy})`,
          backgroundPosition: "center",
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
        }}
      >
        <Header />
        <Hero />
        <Grid
          container
          spacing={2}
          sx={{
            background: `rgba(0,18,51, 1.0)`,
            paddingLeft: 5,
            paddingRight: 5,
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Grid xs={6} md={8}>
            <Grid
              spacing={2}
              container
              sx={{ alignItems: "center", padding: 4 }}
            >
              <Grid item>
                <a href="https://twitter.com/lypherapp">
                  <TwitterIcon sx={{ color: "white" }} />
                </a>
              </Grid>
              <Grid item>
                <a href="https://discord.gg/h7y5UkU7">
                  <Discord style={{ width: 26 }} />
                </a>
              </Grid>
            </Grid>
          </Grid>
          <Grid xs={6} md={4} sx={{ textAlign: "right" }}>
            <Button
              onClick={handlePolicy}
              variant="text"
              style={{ color: "white", fontSize: 8 }}
            >
              Privacy Policy
            </Button>
          </Grid>
          <Typography sx={{ color: "white", fontSize: 9, marginBottom: 10 }}>
            Developed by Big Bear Bytes, Copyright 2022
          </Typography>
        </Grid>
      </Grid>
      {showPolicy && (
        <div style={{ display: "flex", flexDirection: "column" }}>
          <Button
            onClick={handlePolicy}
            color="warning"
            variant="text"
            style={{ alignSelf: "flex-end", margin: 8 }}
          >
            Close
          </Button>
          <PrivacyPolicy />
        </div>
      )}
    </ThemeProvider>
  );
};
export default App;
